@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
 */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, button,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  text-align: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  font-family: 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

:root {
  --accent-color-hsl: 265, 46.8%, 30.2%;
  --accent-color: #472971;
  --primary-color-hsl: 328, 35.6%, 65.9%;
  --primary-color: #c789aa;
  --secondary-color-hsl: 344, 100%, 73.7%;
  --secondary-color: #ff799d;
}

.page {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  will-change: opacity;
}

.page--menu-is-open {
  opacity: .3;
}

.background {
  background-image: -webkit-linear-gradient(290deg, #431e4c, #f7c3b2);
  background-image: linear-gradient(160deg, #431e4c, #f7c3b2);
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: -1;
}

.background__item {
  position: absolute;
  top: 0;
  left: 0;

  background-color: rgba(255, 255, 255, 0.07);
  height: 100px;
  width: 100px;
  border-radius: 20px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.01);

  will-change: transform;
}

.dashboard-page {
  padding: 32px;
}

.dashboard-page__categories {
  margin-left: -32px;
  margin-right: -32px;
  padding-left: 32px;
  padding-right: 32px;
}

.title {
  color: #fdfcfc;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: .08em;
  margin-bottom: 0;
  margin-top: 0;
}

.title::after {
  margin-top: .5em;
  margin-bottom: 2em;
  display: block;

  color: transparent;
  content: 'wwww';
  font-size: 0.7em;
  letter-spacing: normal;
  letter-spacing: initial;
  -webkit-text-decoration: wavy underline hsla(var(--secondary-color-hsl), .65);
          text-decoration: wavy underline hsla(var(--secondary-color-hsl), .65);
}

.money-display__label {
  margin-bottom: .3em;

  color: var(--primary-color);
  font-size: .8em;
  font-weight: bold;
  letter-spacing: .09em;
}

.money-display--monocromatic > .money-display__label {
  color: hsla(0, 0%, 100%, .5);
}

.money-display__value {
  color: var(--accent-color);
  font-size: 2.2em;
  font-weight: bold;
  letter-spacing: .09em;
}

.money-display--monocromatic > .money-display__value {
  color: hsl(0, 0%, 100%);
}

.money-display__value::after {
  color: hsla(0, 0%, 10%, .25);
  content: '$';
  font-weight: normal;
}

.money-display--monocromatic > .money-display__value::after {
  color: hsla(0, 0%, 100%, .5);
}

.categories-carousel__card {
  margin-top: 60px;
  padding: 24px;
}

.categories-carousel__progress {
  -webkit-transform: translateY(-48px);
          transform: translateY(-48px);
}

.carousel {
  overflow-x: auto;
  -webkit-scroll-snap-type: x mandatory;
      -ms-scroll-snap-type: x mandatory;
          scroll-snap-type: x mandatory;
  scroll-padding: 32px;

  -ms-overflow-style: none;
  scrollbar-width: none;
}

.carousel::-webkit-scrollbar { 
  display: none;
}

.carousel__slides {
  display: grid;
  grid-auto-columns: auto;
  grid-auto-flow: column;
  grid-gap: 24px;
}

/* this ::after is a workaround to a misterious think that happens
 * https://codepen.io/vhoyer/pen/vqmGmV
 * */
.carousel__slides::after {
  content: '';
  display: block;
  width: 6.5px;
}

.carousel__slides > * {
  scroll-snap-align: start;
}

.card {
  background-color: #fff;
  border-radius: 20px;
}

.circular-progress {
  background-color: var(--accent-color);
  padding: 16px;
  width: 60px;
  height: 60px;
}

.circular-progress__svg {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.circular-progress__bar {
  stroke: hsla(0, 0%, 100%, 0.4);
}

.toolbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: hsla(0, 0%, 100%, 0.5);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  min-height: 50px;
  padding-left: 16px;
  padding-right: 16px;

  display: grid;
  grid-auto-flow: column;
  align-items: center;
  justify-items: center;

  -webkit-transition: background-color .3s;

  transition: background-color .3s;
  will-change: background-color;
}

.toolbar > :not(.toolbar__action-button) {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  will-change: opacity;
}

.toolbar--menu-is-open {
  background-color: hsla(0, 0%, 100%, 0.2);
}

.toolbar--menu-is-open > :not(.toolbar__action-button) {
  opacity: 0.3;
}

.toolbar__item--inactive {
  -webkit-filter: contrast(0) opacity(0.5);
          filter: contrast(0) opacity(0.5);
}

.toolbar__action-button {
  position: relative;
  top: -16px;

  align-self: end;
  margin-left: auto;
}

.action-button {
  z-index: 0;
}

.action-button__button {
  display: flex;
  align-items: center;
  justify-items: center;
  padding: 24px;
}

.action-button__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.action-button__icon {
  -webkit-transition: -webkit-transform .4s;
  transition: -webkit-transform .4s;
  transition: transform .4s;
  transition: transform .4s, -webkit-transform .4s;
  will-change: transform;
}

.action-button__icon--is-open {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

.action-button__menu {
  position: absolute;
  bottom: 100%;
  right: 0;

  display: grid;
  grid-gap: 16px;

  padding-bottom: 16px;
  min-width: 17rem;

  pointer-events: none;
}

.action-button__menu--is-open {
  pointer-events: auto;
}

.action-button__menu > * {
  opacity: 0;
  -webkit-transform: translateY(20vmin);
          transform: translateY(20vmin);
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
  will-change: transform, opacity;
}

.action-button__menu--is-open > * {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.transaction-menu-item {
  padding: 16px;

  -webkit-transition-property: all;

  transition-property: all;
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
}

.transaction-menu-item--is-expanded {
  position: fixed;
  bottom: 4px;
  left: 4px;
  right: 4px;
  z-index: 1;
}

.transaction-menu-item__title {
  width: 100%;
}

